import NavLink from '../components/NavLink'

import {connect} from 'react-redux'
import {setNavHeaderType} from '../actions/headerActions'

const mapDispatchToProps = (dispatch) => {
    return{
        onNavLinkClick : (type) => {
            dispatch(setNavHeaderType(type))
        }
    }
}

const mapStateToProps = (state, ownProps) => {
    var navType = state.get('currentNavHeaderType');
    return {'active': navType == ownProps.navHeaderType ? true : false}
}

export default connect(mapStateToProps,mapDispatchToProps)(NavLink);



