import {Map} from 'immutable';

export default function(state = Map({currentNavHeaderType:'index'}), action) {
  switch (action.type) {
    // case 'SET_STATE':
    //   return resetVote(setState(state, action.state));
    case 'SET_NAV_HEADER_TYPE':
      return state.set('currentNavHeaderType', action.navHeaderType);
    default:
      return state;
  }
}



