import React from 'react'

const HomeAbout = ()=>{
  return(
    <div className=" home-about">
    <div className="container">
		<h3 className="tz-title-3">我们的服务</h3>
		<h4>尽管作为一个创业团队，我们却不是新人。团队的成员均来自国内，国际知名公司。有丰富而广泛的软件设计、开发经验。
      Seed致力于为互联网/传媒公司提供优秀的应用设计和开发，
    并协助您建立良好的产品体验。</h4>
		<div className="grid_2 text-center">
			<div className="col-md-4 box_1">
				<span className="top-icon1"> </span>
				<h3>Design</h3>
				<p>与产品和研发团队深度沟通，梳理需求与流程,
           对产品功能进行定义.输出符合需求的原型设计,视觉设计,动效设计以及场景设计</p>
			</div>
			<div className="col-md-4 box_1">
				<span className="top-icon2"> </span>
				<h3>Development</h3>
				<p>
          我们提供三个层次的研发与输出：根据设计原稿输出响应式页面,根据团队研发能力选择适合
          的App/Web架构与服务器架构以及完整的产品研发与上线
        </p>
			</div>
			<div className="col-md-4 box_1">
				<span className="top-icon3"> </span>
				<h3>UE/UX</h3>
				<p>用户体验优化：用户画像,目标用户调研
          ,用户行为研究,用户视觉倾向研究,用户体验研究,情感化场景模拟</p>
			</div>
			<div className="clearfix"> </div>
		</div>
	</div></div>

  )
}

export default HomeAbout;



