import React from 'react'
import {Link} from 'react-router'

const Case = (props)=>{
  return(
      <div className="row">
        {props.cases.map((entry,index) =>
          <div className="col-xs-12 col-md-4 case" key={index}>
            <Link to={'/detail?type='+props.type+'&index='+index}><img src={'../images/'+entry.cover} className="case-image"/></Link>
          </div>)}
      </div>
  )
}

export default Case;



