import React from 'react'
import {Link} from 'react-router'

const NavLink = (props)=>{
  return(
    <li className="pull-right">
      <Link to={props.navTo}
        className={props.active == true? 'item selected' : 'item'}
        onClick={()=>{props.onNavLinkClick(props.navHeaderType)}}
        >
        {props.navText}
      </Link>
    </li>
  )
}

export default NavLink;



