import React from 'react'
import {Link} from 'react-router'
import NavLinkContainer from '../containers/NavLinkContainer'
import ContactPopup from './ContactPopup'

require ('../styles/header.css')
let logo = require('../images/seed.png')

class Header extends React.Component{
  constructor(props){
    super(props)
  }

  render(){
    return(
      <div className="container-fluid">

        <div className="row header">
          <div className="col-md-2 col-xs-3">
              <Link to='/'><img src={logo} className="logo"/></Link>
          </div>

          <div className="col-md-10 col-xs-9">
      			<ul className="">
              <NavLinkContainer navTo='/about' navHeaderType='about' navText='我们'></NavLinkContainer>
              <NavLinkContainer navTo='/case' navHeaderType='case' navText='案例'></NavLinkContainer>
              <NavLinkContainer navTo='/' navHeaderType='index' navText='首页'></NavLinkContainer>
      			</ul>
          </div>
        </div>

    </div>
    )
}
}

export default Header;



