import React from 'react'
const jsonData = require('../data.json');

class Detail extends React.Component
{
  constructor(props){
    super(props);
    const index = this.props.location.query.index;
    const type = this.props.location.query.type;
    this.state = {item:jsonData[type][index]}
  }

  render(){
    return(
      <div className="detail-case">
      <div className="container container-full">
        <h3 className="detail-name">
          <i className="fa fa-codepen" aria-hidden="true"></i>
          {this.state.item.name}
        </h3>
        <h4 className="detail-description">{this.state.item.description}</h4>
        {
          this.state.item.pics.map((entry, index)=>
            <div key={index}><img src={entry} className="detail-image"/></div>
          )
        }
      </div>
    </div>
    )
  }

}

export default Detail;



