import React from 'react'
import ContactPopup from './ContactPopup'
require ('../styles/footer.css')

class Footer extends React.Component{

    constructor(props){
      super(props);
      this.state={visible:false}
    }

    openPopupContact(){
      this.setState({visible:true});
    }

    handleVisible(visible)
    {
      this.setState({visible:visible});
    }

    render(){
      return(
        <div>
          <ContactPopup ref='contactPopup' visible={this.state.visible} handleVisible={this.handleVisible.bind(this)}></ContactPopup>
          <div className="footer">
          <div className="container">
            <div className="row text-center">
                <div className="col-md-4 col-md-offset-4">
                    <i className="fa fa-qq" aria-hidden="true"></i>
                    <i className="fa fa-weixin" aria-hidden="true" onClick={this.openPopupContact.bind(this)}></i>
                    <i className="fa fa-phone" aria-hidden="true"></i>
                </div>
            </div>
            <div className="row text-center">
              © Seed Studio All Rights Reserved. | 备案: 蜀ICP备11020230号
            </div>
          </div>
          </div>
      </div>
      )
    }
}


export default Footer;



