import React from 'react'
require('../styles/popup.css')

class ContactPopup extends React.Component
{
  constructor(props){
    super(props);
    this.state={visible:props.visible}
  }

  closePopup(){
    this.setState({visible:false})
    this.props.handleVisible(false);
  }

  componentWillReceiveProps(nextProps){
    this.setState({
      visible: nextProps.visible
    });
  }

  render(){
    var element = '';
    if(this.state.visible)
    {
      element =
      (<div className="row popup">
        <div className='col-xs-12 col-md-4 col-md-offset-4 popup-container'>
          <h2>Title</h2>
          <h4>Test teslktjlkesjtlkewj </h4>
          <div className="popup-close" onClick={this.closePopup.bind(this)}>
            <i className="fa fa-times" aria-hidden="true"></i>
          </div>
        </div>
      </div>)
    }
    return(
      <div>
        {element}
      </div>

    )
  }

}

export default ContactPopup;



