import React from 'react'

const HomeCustomer = ()=>{
  return(
    <div className="home-customer">
    <div className="container">
      <h3 className="tz-title-3">我们的客户</h3>
      <h4>尽管作为一个创业小团队，我们却已为超过20家企业提供数百昼夜的互联网技术服务。其中既有传媒独角兽如MSL Group、世美，也有新锐互联网公司如咕咚，纵游。</h4>
      <div className="col-xs-12 col-md-8 col-md-offset-2 customer-item">
        <img src="../images/customer.png" className="customer-img"></img>
      </div>
    </div>
  </div>
  )
}

export default HomeCustomer;



